<template>
<div class="first_part">
  <header class="part_headers">
    <h2>
      
    </h2>
    <div class="currentpage">
      <div class="textpage"> {{currentpage}}/{{allpage}}</div>
      <div class="progress" v-show="!!allpage">
        <span v-for="page in allpage" :key="page" :class="addcolor(page)"></span>
      </div>
    </div>
  </header>
  <section class="question_wrap" ref="question_wrap">
    <ol class="question_content" ref="question_content">
      <li v-for="(question,index) in questions" :key="index">
        <h4>{{question.num}}. {{question.title}}</h4>
        <div class="options">
          <el-radio-group @change="setoption(question)" v-model="question.value" :ref="parseInt(question.num)">
            <el-radio :label="1"></el-radio>
            <el-radio :label="2"></el-radio>
            <el-radio :label="3"></el-radio>
            <el-radio :label="4"></el-radio>
            <el-radio :label="5"></el-radio>
          </el-radio-group>
        </div>
      </li>
    </ol>
  </section>
  <footer class="setStep">
    <div class="previousstep" v-if="currentpage!=1" @click="previousstep">
      
    </div>
    <div class="nextstep" :class="toggleClass?'yes':'no'" @click="nextstep">
      {{currentpage==allpage?'':''}}
    </div>
  </footer>
</div>
</template>

<script>
import BScroll from 'better-scroll'
import tools from 'utils/tools'
import router from 'src/router'
import {
  Indicator,
  Toast,
  MessageBox
} from 'mint-ui';
import {
  getQuestionnaireList,
  addQuestionnaireList
} from 'api/question'
export default {
  name: 'firstpart',
  data: function() {
    let openId = tools.getCookie('openId')
    return {
      openId,
      currentpage: 0,
      allpage: 0,
      questions: [],
      allquestions: [],
      scroll: null,
      pullup: true,
      loadMore: true,
    }
  },
  watch: {
    currentpage(val) {
      this.getPageData(parseInt(val))
    }
  },
  computed: {
    toggleClass() {
      return this.questions.length && this.questions.every(item => {
        return item.value != ''
      })
    }
  },
  methods: {
    nextstep() {
      if (this.toggleClass) {
        if (this.currentpage == this.allpage) {
          MessageBox({
            title: '',
            showCancelButton: true,
            cancelButtonText: '',
            confirmButtonText: '',
            confirmButtonClass: 'confirmButtonClass',
            cancelButtonClass: 'cancelButtonClass'
          }).then(action => {
            if (action == 'confirm') {
              let partone = this.allquestions.map(item => {
                return {
                  "baseQuestionnaireContentId": item.num,
                  "type": item.value
                }
              })
              Indicator.open('...');


              // tools.rmStorage(`${this.openId}_allquestions`)
              // this.$router.replace({
              //   name: 'secondpart'
              // })

              addQuestionnaireList(partone).then(res => {
                if (res.data.Result == 'success') {
                  Indicator.close();
                  tools.setStorage('questionpart', 2)
                  tools.rmStorage(`${this.openId}_allquestions`)
                  this.$router.replace({
                    name: 'secondpart'
                  })
                }else{
                  Indicator.close();
                  Toast(res.data.message)
                }
              })
            } else {
              console.log('')
            }
          });
        } else {
          this.currentpage++
            this.setScroll()
          this.scroll.scrollTo(0, 0)
        }
      } else {
        Toast(' ~ ~ ')
        let notSelect = null;
        for (let i = 0, len = this.questions.length; i < len; i++) {
          if (!this.questions[i].value) {
            notSelect = this.questions[i]
            break;
          }
        }
        let notSelectElement = this.$refs[notSelect.num][0].$el.parentNode.parentNode
        notSelectElement.classList.add('warring')
        this.scroll.scrollToElement(notSelectElement, 800)
      }
    },
    previousstep() {
      this.currentpage--
        this.setScroll()
      this.scroll.scrollTo(0, 0)
    },
    addcolor(page) {
      return this.currentpage >= page ? 'addcolor' : '';
    },
    ceshi() {
      console.log(this.questions)
    },
    setoption(val) {
      //
      val.select = true
      tools.setStorage(`${this.openId}_allquestions`, this.allquestions)
      let nextElement = this.$refs[val.num + 1]
      let currentLi = this.$refs[val.num][0].$el.parentNode.parentNode
      if (!!nextElement) {
        this.scroll.scrollToElement(nextElement[0].$el.parentNode.parentNode, 800)
      }
      if (currentLi.classList.contains('warring')) {
        currentLi.classList.remove('warring')
      }
    },
    getPageData(page) {
      this.questions = this.allquestions.filter((item, index) => {
        return page == item.page
      })
    },
    setScroll() {
      this.$nextTick(() => {
        const question_wrap = this.$refs.question_wrap
        const question_content = this.$refs.question_content
        this.scroll = new BScroll(question_wrap, {
          scrollY: true,
          startX: 0,
          startY: 0,
          probeType: 3,
          click: true
        })
        //
        const wrapH = question_wrap.getBoundingClientRect().height
        //
        const contentH = question_content.getBoundingClientRect().height
        // -  = 
        console.log(wrapH - contentH)
        Indicator.close();
      })
    }
  },
  beforeRouteEnter: (to, from, next) => {
    let openId = tools.getCookie('openId')
    Indicator.open('...');
    const PAGETIAO = 12 //
    let CURRENTPAGE = 1 //
    let allpage = 0
    let questionpart = tools.getStorage('questionpart')
    let allquestions = tools.getStorage(`${openId}_allquestions`) || null
    var nextFn = vm => {
      //
      allpage = Math.ceil(allquestions.length / PAGETIAO)
      //
      let lastSelect = null
      for (let i = 0, len = allquestions.length; i < len; i++) {
        if (!allquestions[i].select) {
          lastSelect = allquestions[i]
          break;
        }
      }
      if (lastSelect) {
        CURRENTPAGE = lastSelect.page
        vm.questions = allquestions.filter((item, index) => {
          return CURRENTPAGE == item.page
        })
        vm.currentpage = CURRENTPAGE;
      } else {
        vm.currentpage = allpage;
      }
      vm.allquestions = allquestions
      vm.allpage = allpage
      vm.setScroll()
    }
    if (!!allquestions) {
      //
      Indicator.close();
      const isAllSelect = allquestions.every(item => {
        return !!item.value
      })
      next(nextFn)
    } else {;
      (async() => {
        let contents = await getQuestionnaireList()
        let content = contents.data.content
        if (!!content) {
          allquestions = content.map((item, index) => {
            return {
              num: item.id,
              title: item.content,
              value: '',
              page: parseInt((index) / 12) + 1,
              select: false
            }
          })
          allpage = Math.ceil(allquestions.length / PAGETIAO)
          next(vm => {
            vm.questions = allquestions.filter((item, index) => {
              //page = 
              return CURRENTPAGE == item.page
            })
            vm.allpage = allpage
            vm.allquestions = allquestions
            vm.currentpage = CURRENTPAGE
            vm.setScroll()
          })
        } else {
          Toast('')
        }
      })();
    }
  }
}
</script>
<style lang="less">
@import '../../../styles/mixin.less';
body {
  background-color: #fff !important;
}

.mint-indicator-wrapper {
  z-index: 200;
}

.mint-msgbox {
  .mint-msgbox-header {
    padding: 0;
  }
  #wh(518/@rem, 600/@rem);
  .mint-msgbox-title {
    margin: 105/@rem auto 0;
    width: 300/@rem;
    #sc(30/@rem, #333333);
    line-height: 45/@rem;
    font-weight: normal;
  }
  .mint-msgbox-btns {
    display: block;
    height: auto;
    margin-top: 60/@rem;
    .cancelButtonClass {
      border-right: none;
      display: block;
      #wh(330/@rem, 100/@rem);
      background-color: #D8D8D8;
      #sc(32/@rem, #333);
      margin: 0 auto 40/@rem;
    }
    .confirmButtonClass {
      display: block;
      #wh(330/@rem, 100/@rem);
      background-image: linear-gradient(174deg, #04A2FC 0%, #401CD5 100%);
      #sc(32/@rem, #fff);
      margin: 0 auto;
    }
  }
}
</style>

<style lang="less">
@import '../../../styles/mixin.less';
@head_height: 120/@rem;
@btm_height : 105/@rem;
.first_part {
  height: 100%;
  overflow: hidden;
  .part_headers {
    position: fixed;
    background: #fff;
    top: 0;
    left: 0;
    z-index: 1;
    box-shadow: 1px 1px 1px #007AFF;
    #wh(100%, @head_height);
    #fj;
    padding-top: 30/@rem;
    h2 {
      font-size: 45/@rem;
      margin-left: 21/@rem;
    }
    .currentpage {
      #wh(267/@rem, @head_height);
      padding-right: 27/@rem;
      .textpage {
        text-align: right;
        padding-right: 6/@rem;
      }
      .progress {
        #fj(space-around);
        #wh(100%, 15/@rem);
        span {
          #wh(34/@rem, 100%);
          margin-top: 9/@rem;
          background: #f2f2f2;
        }
        .addcolor {
          background: #007AFF;
        }
      }
    }
  }
  .question_wrap {
    width: 100%;
    margin-top: @head_height + 20/@rem;
    height: calc(~"100%" - @head_height + 100/@rem);
    h4 {
      line-height: 42/@rem;
      font-size: 27/@rem;
      #sc(27/@rem, #151515);
      padding: 20/@rem 0 15/@rem 40/@rem;
    }
    .options .el-radio-group {
      #fj(flex-start);
      flex-wrap: wrap;
      align-items: flex-start;
      padding: 0 33/@rem 21/@rem;
      .el-radio {
        width: 30%;
        height: 70/@rem;
        line-height: 80/@rem;
        margin-left: 21/@rem;
      }
    }
    .question_content {
      width: 100%;
      li {
        width: 100%;
        .el-radio__label {
          font-size: 30/@rem;
        }
        .el-radio__inner {
          margin: 12/@rem auto; // #wh(30/@rem,30/@rem)
        }
      }
    }
  }
  .warring {
    animation: warring 1s infinite;
  }
  @keyframes warring {
    0% {
      box-shadow: 0px 0px 1px 2px red inset;
    }
    50% {
      box-shadow: 0px 0px 0px 0px transparent inset;
    }
    100% {
      box-shadow: 0px 0px 1px 2px red inset;
    }
  }
  .loadingMore {
    width: 100%;
    height: 100/@rem;
    text-align: center;
    background: red;
  }
  // 
  .setStep {
    #fj;
    #wh(750/@rem, 100/@rem);
    box-shadow: 0 0 4px 2px rgba(229, 229, 229, 1);
    position: relative;
    z-index: 200;
    background: #fff;
    text-align: center;
    line-height: @btm_height*8/10;
    div {
      flex: 1;
      #sc(33/@rem, #fff);
    }
    .previousstep {
      background: #ccc;
      border-right: 1px solid #fff;
    }
    .nextstep {}
    //
    .nextstep.yes {
      background-image: linear-gradient(-174deg, #04A2FC 0%, #401CD5 100%);
    }
    .nextstep.no {
      background: #ccc;
    }
  }
}
</style>


